@import "../../styles/mixins";
@import "../../styles/partials.scss";

.clothes {
  position: relative;

  &__addBtn {
    justify-content: center;
    display: flex;
    margin-bottom: 5rem;
    margin-top: 2rem;

  }

  &__icon {
    height:20px;
  }

  &__add {
    display:none;

    @include tablet {
      display: initial;
      color: white;
      border-radius: 2px;
      border: none;
      padding: 0.7rem 0.2rem;
      // background: linear-gradient(#42a1ec,#0070c9);
      // background-image: linear-gradient(to right, #E0EAFC 0%, #CFDEF3 51%, #E0EAFC 100%);
      background-color: #29323c;
      font-size: 1.5rem;
      width: 30vw;
      height: 6vw;
      min-height: 50px;
      max-width:266px;
      max-height: 60px;
    }
    
    @include desktop {
      max-width:377px;
      max-height: 75.5px;
      font-size: 2rem;
      width: 22vw;
    }
  }

  &__add:hover {
    opacity: 0.9;
  }

  &__add-mobile {
      margin-bottom: 2rem;
      background-color: #0070c9;
      color: white;
      border-radius: 50%;
      border: none;
      // background: linear-gradient(#42a1ec,#0070c9);
      background-color: #29323c;
      font-size: 1.5rem;
      font-weight: 600;
      width: 50px;
      height: 50px;

      @include tablet {
        display: none;
      }
  }

  &__add-mobile:hover {
    opacity: 0.9;
  }

  &__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__imgWrap {
    padding: 1rem 1rem;
    background-color: #FBF9F8;
    height: 60vw;
    width:60vw;
    margin: 1rem 1rem;
    box-sizing: border-box;

    @include tablet {
      height: 40vw;
      width: 40vw;

    }

    @include desktop {
      height: 28vw;
      width: 25vw;

    }
  }


  &__img {
    height: 100%;
    width: 100%;
    max-width: 150%;
  }

  &__img:hover {
    transform: scale(1.3);
  }

}
 